<template>
  <div class="page">
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="钱咖入口"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content">
      <div class="c3a fs24 sub-title">安装入口</div>
      <div class="c3a fs13 mrg-t20 mrg-b50 desc">让您更加便捷的使用钱咖</div>
      <div class="img-wrap"><img src="//qianka.b0.upaiyun.com/images/1e618a6b6f85ffee3261d6043da02dd8.png"/></div>
      <div><a class="now-install" @click="openSettingPopup()" href="javascript:;">立即安装</a></div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins.scss";

  .page-content {
    padding-top: 110px;
    text-align: center;
    background: #fff;

    @include font-regular()

    .sub-title {
      height: 33px;
      line-height: 33px;
    }

    .desc {
      height: 18px;
      line-height: 18px;
    }

    .fs24 {
      font-size: 24px;
    }

    .fs13 {
      font-size: 13px;
    }

    .c3a {
      color: #3a3a3a;
    }

    .mrg-t20 {
      margin-top: 20px;
    }

    .mrg-b50 {
      margin-bottom: 50px;
    }

    .img-wrap {
      width: 224px;
      height: 162px;
      margin: 0 auto;

      img {
        width: 100%;
      }
    }

    .now-install {
      display: block;
      width: 172px;
      height: 40px;
      font-size: 13px;
      line-height: 40px;
      border-radius: 2px;
      color: #fff;
      background-color: #ef502b;
      text-decoration: none;
      margin: 60px auto 0;

      @include font-semibold()
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index'
  import { mapState, mapActions } from 'vuex'
  export default {
    components: {
      AnimateHeader
    },

    computed: {
      ...mapState('user', [
        'user_info'
      ])
    },

    mounted () {
      this.fetch()
    },

    methods: {
      ...mapActions('user', [
        'fetch'
      ]),

      back () {
        $router.back('/user')
      },

      openSettingPopup () {
        location.href = this.user_info.webclip
      }
    }
  }
</script>



